<template>
	<div class="detail">
		<header :style="{background:mainColor}" class="header">
			<van-icon name="arrow-left" @click="$router.back()" />
			<p>文章详情</p>
		</header>
		<div class="w">
			<h2 class="title">{{item.title}}</h2>
			<div class="author">
				<div class="left">
					<div class="image">
						<img :src="avatar" alt="">
					</div>
					<p :title="item.author">{{item.author}}</p>
					<span>{{ontime(item.time)}}</span>
				</div>
				<div class="right" @click="attention">
					<van-button round type="default" size="small">
						<van-icon name="plus" v-show="isAttention" />
						{{isAttention?"已关注":"关注"}}
					</van-button>
				</div>
			</div>
			<div class="text">
				{{text}}
			</div>
			<!-- {{typeof(item.imageSrc)}} -->
			<div v-show="imgSrc" class="photo">
				<img :src="photo" v-for="(photo,i) in imgSrc" :key="i">
			</div>
		</div>
		<div class="comment">
			<ul>
				<li v-for="item in commentList" :key="item._id">
					<img :src="item.info.avatar" alt="">
					<div class="content">
						<p class="username">{{item.info.nickname}}</p>
						<p class="con" @click="yesShow(item)">{{item.content}}</p>
						<p class="time">{{ontime(item.create_time)}} <span
								@click="getReplyList(item)">{{item.reply_num}}
								回复</span></p>
						<div v-show="clickItem._id==item._id">
							<van-loading size="24px" type="spinner" v-show="isShow">加载中...</van-loading>
						</div>
						<ul v-show="clickItem._id==item._id" style="width: 120%;margin-left: -44px;">
							<li v-for="item in replyList" :key="item._id">
								<img :src="item.info.avatar" alt="">
								<div class="content">
									<p class="username">{{item.info.nickname}}</p>
									<p class="con">{{item.content}}</p>
									<p class="time">{{ontime(item.create_time)}} <span
											@click="second">{{item.reply_num}}
											回复</span></p>
								</div>
								<div class="nice_count" @click="commentNice(item)">
									<van-icon name="good-job" v-show="item.is_like" color="#ef092c" />
									<van-icon name="good-job-o" v-show="!item.is_like" />
									<span>{{item.like_count}}</span>
								</div>
							</li>
						</ul>
					</div>
					<div class="nice_count" @click="commentNice(item)">
						<van-icon name="good-job" v-show="item.is_like" color="#ef092c" />
						<van-icon name="good-job-o" v-show="!item.is_like" />
						<span>{{item.like_count}}</span>
					</div>
				</li>
			</ul>
		</div>
		<van-popup v-model="show">
			<van-field v-model.trim="msg" ref="msgIpt" @keyup.enter="reviewArticle(clickItem,1)"
				placeholder="请输入你的要回复的内容" />
			<div class="btns">
				<van-button type="default" size="small" @click="noShow">取消</van-button>
				<van-button type="primary" size="small" @click="reviewArticle(clickItem,1)">确认</van-button>
			</div>
		</van-popup>
		<van-share-sheet v-model="showShare" title="立即分享给好友" :options="options" @select="onSelect" />
		<footer class="footerHandle">
			<input type="text" v-model.trim="msg" placeholder="写评论" @keyup.enter="reviewArticle(null,0)">
			<van-icon name="comment-o" @click="reviewArticle(null,0)" />
			<van-icon name="star-o" v-show="!isCollect" @click="collect" />
			<van-icon name="star" v-show="isCollect" color="#0078d4" @click="collect" />
			<van-icon name="like-o" v-show="!isLike" @click="like" />
			<van-icon name="like" v-show="isLike" color="#ef092c" @click="like" />
			<van-icon name="share-o" @click="showShare = true" />
		</footer>
		<van-loading color="#167fe8" class="load" v-show="isLoad" />
	</div>
</template>

<script>
	import {
		mapState
	} from "vuex";
	import {
		Toast,
		ShareSheet,
		Loading,
		Dialog

	} from 'vant';
	import html2canvas from 'html2canvas';
	import QRCode from 'qrcode'
	export default {
		props: ['item'],
		name: 'detail',
		data() {
			return {
				isLoad: false,
				imgSrc: [],
				show: false,
				text: '',
				msg: '',
				isCollect: false,
				isLike: false,
				commentList: [],
				avatar: '',
				clickItem: '',
				replyList: [],
				isShow: false,
				isNice: false,
				isAttention: false,
				a: 0,
				clickLikeCount: 0,
				showShare: false,
				options: [
					[{
							name: '微信',
							icon: 'wechat'
						},
						{
							name: '微博',
							icon: 'weibo'
						},
						{
							name: 'QQ',
							icon: 'qq'
						},
					],
					[{
							name: '复制链接',
							icon: 'link'
						},
						{
							name: '分享海报',
							icon: 'poster'
						},
						{
							name: '二维码',
							icon: 'qrcode'
						},
					],
				],
			}
		},
		components: {
			[ShareSheet.name]: ShareSheet,
			[Loading.name]: Loading,
		},
		computed: {
			...mapState('aboutPerson', ['person_info'])
		},
		methods: {
			onSelect(option) {
				let {
					icon
				} = option;
				this.showShare = false;
				let shareObj = {
					'wechat': this.wechat,
					'weibo': this.weibo,
					'qq': this.qq,
					'link': this.link,
					'poster': this.poster,
					'qrcode': this.qrcode
				}
				shareObj[icon]();
			},
			wechat() {
				this.erweima('50%', '微信扫码查看')
			},
			weibo() {
				window.open(
					`https://service.weibo.com/share/share.php?title=${window.encodeURIComponent(this.item.title)} ${window.encodeURIComponent(`https://chengchaofan.github.io（想看更多文章，欢迎访问小程头条）#`)}&pic=${window.encodeURIComponent(this.item.imageSrc[0])}`
				);
			},
			qq() {
				window.open(`
				https://connect.qq.com/widget/shareqq/index.html
				?
				url=${window.encodeURIComponent(location.href)}
				&
				title=${window.encodeURIComponent(this.item.title)}
				&
				summary=${window.encodeURIComponent(this.text)}
				&
				pics=${window.encodeURIComponent(this.item.imageSrc[0])}
				&
				site=${window.encodeURIComponent('小程头条')}
				`)
			},
			link() {
				let input = document.createElement('input');
				document.body.appendChild(input);
				input.value = location.href;
				input.select();
				document.execCommand('copy');
				input.remove();
				Toast.success('复制成功，快分享给你的小伙伴吧')
			},
			poster() {
				this.isLoad = true;
				html2canvas(document.querySelector(".w"), {
					allowTaint: true,
					useCORS: true
				}).then(canvas => {
					let image = new Image()
					image.src = canvas.toDataURL('image/png');
					this.photoSrc(image, '80%', '长按图片保存')
					// canvas.getContext('2d').drawImage(image, 0, 0);
				});
			},
			qrcode() {
				this.isLoad = true;
				this.erweima('80%', '长按图片保存')
			},
			erweima(w, t) {
				QRCode.toDataURL(location.href)
					.then(url => {
						let image = new Image()
						image.src = url;
						this.photoSrc(image, w, t)
					})
					.catch(err => {
						console.error(err)
					})
			},
			photoSrc(image, width, txt) {
				image.style.width = width;
				let div = document.createElement('div');
				let p = document.createElement('p');
				p.innerHTML = txt
				div.className = "spareImg";
				document.body.appendChild(div);
				this.isLoad = false;
				div.addEventListener('click', function() {
					this.remove()
				});
				div.addEventListener('touchmove', function() {
					this.remove()
				});
				div.addEventListener('wheel', function() {
					this.remove()
				});
				div.appendChild(image);
				div.appendChild(p);
			},
			attention() {
				if (!this.person_info) {
					Toast.fail('您还未登录哦');
					return
				}
				this.isAttention = !this.isAttention;
				if (this.isAttention) {
					Toast({
						message: "关注成功",
						icon: 'success',
						duration: 1200,
					});
					return
				}
				Toast({
					message: "取消关注成功",
					icon: 'fail',
					duration: 1200,
				});
			},
			collect() {
				if (!this.person_info) {
					Toast.fail('您还未登录哦');
					return
				}
				if (this.isCollect) {
					this.$axios({
						url: 'api/remove_fav',
						data: {
							article_id: this.item._id,
							user_id: this.person_info._id
						}
					}).then(
						res => {
							console.log(res.data);
							if (!res.data.code) {
								Toast({
									message: res.data.msg,
									icon: 'fail',
									duration: 1200,
								});
								this.isCollect = false;
								return
							}
							Toast({
								message: res.data.msg,
								icon: 'cross',
								duration: 1200,
							});
						}
					)
					return
				}
				this.$axios({
					url: 'api/add_fav',
					data: {
						"article_id": this.item._id,
						user_id: this.person_info._id
					}
				}).then(
					res => {
						console.log(res.data);
						if (!res.data.code) {
							Toast({
								message: res.data.msg,
								icon: 'success',
								duration: 1200,
							});
							this.isCollect = true;
							return
						}
						Toast({
							message: res.data.msg,
							icon: 'cross',
							duration: 1200,
						});
					}
				)
			},
			like() {
				if (!this.person_info) {
					Toast.fail('您还未登录哦');
					return
				}
				this.clickLikeCount++
				if (this.clickLikeCount > 5) {
					// Toast({
					// 	message: '你一直点 N M 呢'
					// });
					Dialog.alert({
						title: '傻呗?',
						message: '你一直点 N M 呢',
					}).then(() => {
						this.clickLikeCount = 0;
					});
					return
				}
				if (this.isLike) {
					this.$axios({
						url: 'api/unlike',
						data: {
							article_id: this.item._id,
							user_id: this.person_info._id
						}
					}).then(
						res => {
							// console.log(res.data);
							if (!res.data.code) {
								Toast({
									message: res.data.msg,
									icon: 'fail',
									duration: 1200,
								});
								this.isLike = false;
								return
							}
							Toast({
								message: res.data.msg,
								icon: 'cross',
								duration: 1200,
							});
						}
					)
					return
				}
				this.$axios({
					url: 'api/like',
					data: {
						"article_id": this.item._id,
						user_id: this.person_info._id
					}
				}).then(
					res => {
						if (!res.data.code) {
							Toast({
								message: res.data.msg,
								icon: 'success',
								duration: 1200,
							});
							this.isLike = true;
							return
						}
						// console.log(res.data);
						Toast({
							message: res.data.msg,
							icon: 'cross',
							duration: 1200,
						});
					}
				)
			},
			second() {
				Toast({
					message: '这里的区域以后再来探索吧',
					icon: 'like-o',
				});
			},
			commentNice(niceItem) {
				// console.log(niceItem);
				// return
				if (!this.person_info) {
					Toast.fail('您还未登录哦');
					return
				}
				if (niceItem.is_like) {
					this.$axios({
						url: 'api/comment_unlike',
						data: {
							comment_id: niceItem._id,
							user_id: this.person_info._id
						}
					}).then(
						res => {
							// console.log(res.data);
							if (!res.data.code) {
								this.comment();
								this.getReplyList(niceItem, false);
								// console.log(1);
								Toast({
									message: res.data.msg,
									icon: 'fail',
									duration: 1200,
								});
								return
							}
							Toast({
								message: res.data.msg,
								icon: 'cross',
								duration: 1200,
							});
						}
					)
					return
				}
				this.$axios({
					url: 'api/comment_like',
					data: {
						comment_id: niceItem._id,
						user_id: this.person_info._id
					}
				}).then(
					res => {
						if (!res.data.code) {
							this.getReplyList(niceItem, false);
							this.comment();
							Toast({
								message: res.data.msg,
								icon: 'success',
								duration: 1200,
							});
							return
						}
						// console.log(res.data);
						Toast({
							message: res.data.msg,
							icon: 'cross',
							duration: 1200,
						});
					}
				)
			},
			noShow() {
				this.show = false;
				this.msg = "";
			},
			yesShow(comItem) {
				this.clickItem = comItem;
				this.show = true;
				this.$nextTick(() => {
					this.$refs.msgIpt.focus();
				})
			},
			getReplyList(commentItem, b) {
				// console.log(commentItem);
				this.isShow = b === false ? b : true
				if (!(b === false)) {
					if (this.a) {
						this.a--
						this.clickItem = '';
					} else if (!this.a) {
						this.a++
						this.clickItem = commentItem;
					}
				}
				let perId;
				if (this.person_info) {
					perId = this.person_info._id;
				}
				this.replyList = []
				this.$axios({
					url: 'api/get_reply_list',
					data: {
						user_id: perId,
						"reply_comment_id": commentItem._id,
						"article_id": this.item._id,
					}
				}).then(
					response => {
						// console.log(response.data);
						this.isShow = false
						this.replyList = response.data.data
					}
				)
			},
			comment() {
				let perId;
				if (this.person_info) {
					perId = this.person_info._id;
				}
				this.$axios({
					url: 'api/get_comment_list',
					data: {
						"article_id": this.item._id,
						user_id: perId
					}
				}).then(
					res => {
						this.commentList = res.data.data;
					}
				)
			},
			reviewArticle(commentItem, num) {
				if (!this.person_info) {
					Toast.fail('您还未登录哦');
					return
				}
				// console.log(this.msg);
				if (!this.msg) {
					Toast.fail('请输入内容');
					return
				}
				if (commentItem) {
					if (!this.msg) {
						this.$refs.msgIpt.focus();
						return
					}
				}
				this.$axios({
					url: 'api/add_comment',
					data: {
						user_id: this.person_info._id,
						"article_id": this.item._id,
						"content": this.msg,
						"reply_comment_id": commentItem ? commentItem._id : '',
						"comment_type": num
					}
				}).then(
					res => {
						if (!this.code) {
							Toast({
								message: res.data.msg,
								icon: 'success',
								duration: 1200,
							});
							this.show = false;
							this.msg = '';
							this.comment();
							// this.getReplyList(commentItem, true)
							return
						}
						Toast({
							message: res.data.msg,
							icon: 'cross',
							duration: 1200,
						});
					}
				)
			}
		},
		mounted() {
			this.clickLikeCount = 0;
			// console.log(this.item);
			let perId;
			if (this.person_info) {
				perId = this.person_info._id;
			}
			this.$axios({
				url: 'api/get_article_detail',
				data: {
					"article_id": this.item._id,
					user_id: perId
				}
			}).then(
				response => {
					if (response.data.data) {
						this.avatar = response.data.data.avatar;
						this.isCollect = response.data.data.is_fav;
						this.isLike = response.data.data.is_like;
						this.imgSrc = response.data.data.imageSrc;
						this.text = response.data.data.content;
					}
				}
			)
			this.comment()
		},
		beforeDestroy() {
			if (document.querySelector(".spareImg")) {
				document.querySelector(".spareImg").remove()
			}
		}
	}
</script>

<style scoped lang="less">
	.detail {
		width: 100%;
		margin-top: 50px;

		.load {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}

		.w {
			padding: 0 15px;
		}


		.author {
			width: 100%;
			margin-top: 30px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			height: 60px;

			.left {
				display: flex;
				width: 40%;
				flex: 1;
				justify-content: space-between;
				align-items: center;

				img {
					width: 60px;
					height: 60px;
					border-radius: 50%;
					margin-top: 10px;
					margin-right: 10px;
				}

				p {
					padding-top: 12px;
					width: 55%;
					white-space: nowrap;
					overflow: hidden;
					text-overflow: ellipsis;
				}
			}
		}

		.photo {

			img {
				width: 100%;
			}
		}
	}

	.text {
		padding: 20px 15px;
	}

	.van-popup {
		width: 80%;
		padding: 20px;
	}

	.van-share-sheet {
		width: 100%;
	}

	/deep/ .van-share-sheet__options {
		justify-content: space-evenly;
	}

	.btns {
		display: flex;
		justify-content: space-around;
		margin-top: 10px;
	}

	.comment {
		margin-bottom: 60px;
		margin-top: 20px;

		li {
			display: flex;
			justify-content: space-between;
			padding: 0 15px;
			margin: 14px 0;

			img {
				width: 50px;
				height: 50px;
			}

			.content {
				flex: 1;
				margin-left: 15px;

				.username {
					font-size: 14px;
					color: #7f9ecb;
				}

				.con {
					margin: 3px 0;
				}

				.time {
					font-size: 12px;

					span {
						display: inline-block;
						padding: 2px 6px;
						border: 1px solid #ccc;
						border-radius: 10px;
						margin-left: 5px;
					}
				}
			}
		}
	}
</style>
