<template>
	<div @touchmove="noShow(1)" @mousewheel="noShow(1)">
		<div class="search">
			<form action="/" style="width: 100%;">
				<van-search v-model.trim="search" show-action @input="fuzzySearch" placeholder="请输入搜索关键词"
					@search="toSearch(search)" @cancel="$router.back()" @blur="noShow(0)" @focus="fuzzySearch()" />
			</form>
		</div>
		<div class="history" v-show="isHistoryShow">
			<van-cell value="历史搜索:">
				<!-- 使用 right-icon 插槽来自定义右侧图标 -->
				<template #right-icon>
					<van-icon name="delete-o" @click="clear" />
				</template>
			</van-cell>
			<ul class="search_history">
				<li v-for="item,i in search_history" :key="item.id" @click="toSearch(item.title)">
					<span>{{item.title}}</span>
					<van-icon name="cross" @click.stop="remove(i)" />
				</li>
			</ul>
		</div>
		<ul class="fuzzyList" v-show="fuzzy">
			<li v-for="item in fuzzyList" :key="item._id" @click="toSearch(item.title)">
				<van-cell :value="item.title" />
			</li>
		</ul>
		<van-loading size="24px" color="#0094ff" vertical v-show="isLoad">加载中...</van-loading>
		<van-empty class="custom-image" image="http://s1.hdslb.com/bfs/static/jinkela/mstation-h5/assets/notFound.png"
			description="什么都没有找到啊 T_T" v-show="isNoShow" />
		<ul class="list commonList" v-show="isShow">
			<li v-for="item in searchList" :key="item._id" @click="toDetail(item)">
				<div class="list_top">
					<p>{{item.title}}</p>
					<div v-show="item.imageSrc">
						<img :src="imgSrc" v-for="(imgSrc,i) in item.imageSrc" v-lazy="imgSrc" :key="i">
					</div>
				</div>
				<div class="list_bottom">
					<span class="author">作者 {{item.author}}</span>
					<span>{{item.comment}} 评论</span>
					<span>发布日期：{{new Date(item.time).toLocaleDateString()}}
						{{new Date(item.time).toLocaleTimeString()}}</span>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	import {
		nanoid
	} from 'nanoid';
	import {
		Toast,
		Loading,
		Search,
		Empty
	} from 'vant';
	export default {
		name: 'search',
		components: {
			[Loading.name]: Loading,
			[Search.name]: Search,
			[Empty.name]: Empty
		},
		data() {
			return {
				search: '',
				isSend: false,
				searchList: [],
				fuzzyList: [],
				search_history: [],
				isShow: false,
				isHistoryShow: true,
				isLoad: false,
				isNoShow: false,
				fuzzy: false,
			}
		},
		methods: {
			remove(i) {
				this.search_history.splice(i, 1)
				localStorage.setItem('search_history', JSON.stringify(this.search_history));
			},
			noShow(n) {
				if (n) {
					this.fuzzy = false;
					document.querySelector(".van-field__control").blur();
					return
				}
				setTimeout(() => {
					this.fuzzy = false;
				}, 200)
			},
			toSearch(title) {
				if (!title) return
				this.isHistoryShow = false
				title = title.trim()
				this.search = title;
				if (this.search) {
					this.isShow = true;
					this.fuzzy = false;
				}
				if (this.isSend) return
				this.isSend = true;
				this.isLoad = true
				this.$axios({
					url: 'api/search',
					data: {
						key_word: title
					}
				}).then(
					res => {
						this.$axios({
							url: 'api/search',
							data: {
								key_word: title,
								limit: res.data.count
							}
						}).then(
							res => {
								this.isLoad = false
								this.isHistoryShow = false
								this.searchList = res.data.data;
								let item = this.search_history.find(val => val.title == title);
								if (item) {
									this.search_history = this.search_history.filter(val => val != item);
								}
								this.search_history.unshift({
									title,
									id: nanoid()
								});

								localStorage.setItem('search_history', JSON.stringify(this.search_history));
								this.isSend = false
							}
						);
					}
				);
			},
			fuzzySearch() {
				if (this.search) {
					this.isHistoryShow = false
				} else {
					this.isHistoryShow = true;
				}
				if (this.isSend) return
				this.isSend = true;
				this.$axios({
					url: 'api/search',
					data: {
						key_word: this.search
					}
				}).then(
					res => {
						this.fuzzy = true;
						this.fuzzyList = res.data.data;
						this.isSend = false
					}
				);
			},
			clear() {
				localStorage.removeItem('search_history');
				this.search_history = [];
			},
			isNoMore() {
				if (!this.isLoad && !this.searchList.length && !this.isHistoryShow && !this.fuzzy) {
					this.isNoShow = true
				} else {
					this.isNoShow = false
				}
			}
		},
		watch: {
			search(newValue) {
				if (!newValue) {
					this.isShow = true;
					this.fuzzy = false;
					this.searchList = [];
				}
				this.isNoMore()
			},
			isLoad(newValue) {
				this.isNoMore()
			},
		},
		beforeRouteEnter(to, from, next) {
			if (from.path !== "/Detail") {
				setTimeout(() => {
					document.querySelector(".van-field__control").focus();
				}, 300)
			}
			next()
		},
		activated() {
			this.search_history = [...new Set(JSON.parse(localStorage.getItem('search_history')))];
		},
	}
</script>

<style scoped lang="less">
	.search {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		padding: 10px;
		background: #fff;
	}

	.fuzzyList {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 5;
		background: #fff;
		box-shadow: 0 4px 8px #eee;
	}

	.fuzzyList,
	.commonList {
		margin-top: 65px;
	}

	.fuzzyList li span {
		width: 100%;
		display: block;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.history {
		margin-top: 60px;
		background: #fff;
		padding-bottom: 20px;
	}

	.search_history {
		display: flex;
		flex-wrap: wrap;

		li {
			padding: 4px 8px;
			border: 1px solid #eee;
			border-radius: 20px;
			font-size: 14px;
			margin: 5px 10px;

			span {
				margin-right: 15px;
			}

			i {
				font-size: 12px;
			}
		}
	}

	.van-cell {
		align-items: center;
	}

	.van-loading {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}

	.van-empty {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}

	/deep/ .van-empty__image {
		width: 240px;
	}

	/deep/ .van-empty__description {
		padding: 0;
	}
</style>
