<template>
	<div>
		<van-pull-refresh v-model="refreshing" @refresh="onRefresh">
			<van-list v-model="loading" :finished="finished" finished-text="没有更多了" offset="50" @load="onLoad">
				<ul class="list commonList">
					<li v-for="item in listData" :key="item._id" @click="toDetail(item)">
						<div class="list_top">
							<p>{{item.title}}</p>
							<div v-show="item.imageSrc.length">
								<img :src="imgSrc" v-for="(imgSrc,i) in item.imageSrc" :key="i">
							</div>
						</div>
						<div class="list_bottom">
							<span class="author">作者: {{item.author}}</span>
							<span>{{item.comment}} 评论</span>
							<span>发布日期：{{new Date(item.time).toLocaleDateString()}}
								{{new Date(item.time).toLocaleTimeString()}}</span>
						</div>
					</li>
				</ul>
			</van-list>
		</van-pull-refresh>
	</div>
</template>

<script>
	import {
		PullRefresh,
		List,
	} from 'vant';
	export default {
		components: {
			[PullRefresh.name]: PullRefresh,
			[List.name]: List,
		},
		data() {
			return {
				listData: [],
				loading: false,
				finished: false,
				refreshing: false,
				skip: 0,
				limit: 10,
				cate_Id: ''
			}
		},
		methods: {
			onLoad() {
				if (!this.cate_Id) {
					// console.log(this.cate_Id);
					setTimeout(() => {
						this.onLoad()
					}, 50)
					return
				}
				this.$axios({
					url: `api/get_article_list`,
					data: {
						"cate_id": this.cate_Id,
						"skip": this.skip,
						"limit": this.limit
					}
				}).then(
					res => {
						this.listData = res.data.data;
						this.loading = false;
						this.refreshing = false;
						this.skip++;
						this.limit += 10;
						if (this.limit > res.data.count) {
							this.finished = true;
						}
					}
				)

			},
			onRefresh(cateId) {
				// 将 loading 设置为 true，表示处于加载状态
				this.finished = false;
				this.loading = true;
				this.skip = 0;
				this.limit = 10;
				this.cate_Id = cateId ? JSON.parse(JSON.stringify(cateId)) : this.cate_Id;
				this.onLoad(this.cate_Id);
			},
		},
		mounted() {
			this.$bus.$on("cateId", this.onRefresh);
			this.$axios({
				method: 'get',
				url: `api/get_cate_list`
			}).then(
				res => {
					this.cate_Id = res.data.data[0]._id;
				}
			)
		}
	}
</script>

